<template>
  <b-container fluid>
    <b-row>
      <b-col sm="12">
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title">User List</h4>
          </template>
          <template v-slot:headerAction>
            <b-button variant="primary" :to="{ name: 'user.add' }">Add User</b-button>
          </template>
          <template v-slot:body>
            <ag-grid-vue
                id="ag-grid"
                class="ag-theme-material border height-500"
                :columnDefs="columnDefs"
                :rowData="userList"
                :floatingFilter="true"
                :modules="modules"
                rowSelection="multiple"
            ></ag-grid-vue>
          </template>
        </iq-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import "ag-grid-community/dist/styles/ag-grid.min.css";
import "ag-grid-community/dist/styles/ag-theme-material.css";
import {AllCommunityModules} from "@ag-grid-enterprise/all-modules";
import {core} from "../../config/pluginInit";
import {AgGridVue} from "ag-grid-vue";
import ActionUser from "./Components/ActionUser";
import {mapActions, mapState} from 'vuex';

export default {
  name: "UserList",
  mounted() {
    core.index();
  },
  created() {
    this.getUsers();
  },
  data() {
    return {
      columnDefs: null,
      rowData: [],
      modules: AllCommunityModules
    };
  },
  components: {
    AgGridVue,
    ActionUser
  },
  beforeMount() {
    this.columnDefs = [
      {headerName: "Nombre", field: "fullname", sortable: true, filter: true},
      {headerName: "Correo electronico", field: "email", sortable: true, filter: true},
      {headerName: "Rol", field: "role", sortable: true, filter: true},
      {headerName: "Actions", cellRendererFramework: ActionUser}
    ];
  },
  methods: {
    ...mapActions('User', ['getUsers'])
  },
  computed: {
    ...mapState('User', ['userList'])
  }
};
</script>
